*{
    font-family: 'Roboto', sans-serif;
}

.post__container {
    background-color: white;
    max-width: 500px;
    padding: 5px 0px;
    border: 1px solid lightgray;
    margin: 20px 0px;
}

.post__image {
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    width: 100%;
    object-fit: contain;
    margin: 5px 0px;
}

.post__top {
    display: flex;
    align-items: center;
    margin: 4px 0px;
}

.post__avatar {
    border-radius: 100px;
    width: 30px;
    margin: 0px 5px;
}

.post__bottom {
    margin-left: 5px;
}

span{
    font-size: smaller;
    color: gray;
}
